ontainer{position:fixed;left:0;top:0;width:100%;height:100%;display:-webkit-flex;display:flex;-webkit-flex-flow:row nowrap;flex-flow:row nowrap;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.loader{background:lightgrey;height:10px;width:50%;position:relative}.loader .loading-bar{background:red;position:absolute;left:0;top:0;height:100%;transition:width .2s ease}

